////
/// Content Legacy
/// @group content
////

@include legacy(ie11) {
  #{ns(content-media)} {
    &__img {
      //le padding permettant de dfinir le ratio d'aspect est appliqu sur un pseudo-lment before qui hrite de la modification de taille --lg et --sm de l'lment img ou vid
      @include aspect-ratio-legacy;
    }

    &__transcription {
      #{ns(btn)} {
        @include icon-legacy(menu-2-fill, sm, before);
      }
    }
  }
}
